/* ==============
   Global Reset
   ============== */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Inter",
    "Segoe UI", sans-serif;
  background: #020617; /* slate-950 style */
  color: #e5e7eb; /* slate-200 */
}

/* Smooth scrolling for internal anchor links */
a {
  color: inherit;
  text-decoration: none;
}

button {
  border: none;
  background: none;
  color: inherit;
  cursor: pointer;
  font: inherit;
}

/* Scrollbar styling (optional) */
body::-webkit-scrollbar {
  width: 8px;
}
body::-webkit-scrollbar-track {
  background: #020617;
}
body::-webkit-scrollbar-thumb {
  background: #1f2937;
  border-radius: 999px;
}

/* ==============
   Layout Helpers
   ============== */

.app {
  min-height: 100vh;
  background: radial-gradient(circle at top, #1d2538 0, #020617 50%);
  color: #e5e7eb;
}

.container {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1.25rem;
}

.section {
  padding: 6rem 0; /* a bit more height so user scrolls more */
  position: relative;
}

.section-alt {
  background: radial-gradient(circle at top left, #111827 0, #020617 55%);
}

.section-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #f9fafb;
  text-align: center;
}

.section-subtitle {
  font-size: 0.95rem;
  color: #9ca3af;
  max-width: 520px;
  text-align: center;
  margin: 0.25rem auto 0;
}

/* Add some subtle dividers between sections */
.section + .section,
.section + .section-alt,
.section-alt + .section {
  border-top: 1px solid rgba(55, 65, 81, 0.7);
}

/* =========
   Navbar
   ========= */

.navbar {
  position: sticky;
  top: 0;
  z-index: 20;
  backdrop-filter: blur(16px);
  background: rgba(2, 6, 23, 0.9);
  border-bottom: 1px solid rgba(31, 41, 55, 0.9);
}

.navbar-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.9rem 0;
}

.navbar-logo {
  display: flex;
  align-items: center;
  gap: 0.45rem;
  cursor: pointer;
}

.logo-dot {
  width: 11px;
  height: 11px;
  border-radius: 999px;
  background: linear-gradient(135deg, #6366f1, #22c55e);
  box-shadow: 0 0 12px rgba(56, 189, 248, 0.7);
}

.logo-text {
  font-weight: 600;
  letter-spacing: 0.08em;
  font-size: 0.9rem;
  text-transform: uppercase;
  color: #e5e7eb;
}

.navbar-links {
  display: flex;
  align-items: center;
  gap: 1.3rem;
}

.navbar-link {
  font-size: 0.85rem;
  color: #9ca3af;
  position: relative;
  padding-bottom: 2px;
  transition: color 0.2s ease;
}

.navbar-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 0;
  height: 1.5px;
  border-radius: 999px;
  background: linear-gradient(90deg, #6366f1, #22c55e);
  transition: width 0.2s ease;
}

.navbar-link:hover {
  color: #e5e7eb;
}

.navbar-link:hover::after {
  width: 100%;
}

/* =========
   Hero
   ========= */

.hero {
  padding-top: 5rem;
}

.hero-content {
  display: grid;
  grid-template-columns: minmax(0, 3fr) minmax(0, 2fr);
  gap: 3rem;
  align-items: center;
}

@media (max-width: 768px) {
  .hero-content {
    grid-template-columns: 1fr;
    padding-top: 1rem;
  }
}

.hero-text {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.hero-tag {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.18em;
  color: #9ca3af;
}

.hero-title {
  font-size: clamp(2.2rem, 3.1vw, 3rem);
  font-weight: 700;
  line-height: 1.1;
}

.hero-title span {
  background: linear-gradient(90deg, #6366f1, #22c55e, #22c5b4);
  -webkit-background-clip: text;
  color: transparent;
}

.hero-subtitle {
  font-size: 0.95rem;
  color: #9ca3af;
  max-width: 460px;
}

.hero-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

/* ===== Buttons ===== */

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  font-size: 0.85rem;
  font-weight: 500;
  padding: 0.7rem 1.35rem;
  border-radius: 999px;
  transition: background 0.2s ease, color 0.2s ease, border-color 0.2s ease,
    transform 0.15s ease, box-shadow 0.15s ease;
}

.btn.primary {
  background: linear-gradient(135deg, #6366f1, #22c55e);
  color: #020617;
  box-shadow: 0 16px 30px rgba(79, 70, 229, 0.35);
}

.btn.primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 20px 40px rgba(79, 70, 229, 0.5);
}

.btn.outline {
  border: 1px solid #4b5563;
  color: #e5e7eb;
  background: rgba(15, 23, 42, 0.9);
}

.btn.outline:hover {
  border-color: #6b7280;
  background: rgba(15, 23, 42, 1);
}

/* ===== Hero Card ===== */

.hero-card {
  border-radius: 1.5rem;
  border: 1px solid rgba(55, 65, 81, 0.9);
  background: radial-gradient(circle at top, #1f2937, #020617);
  padding: 1.75rem 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.9);
  position: relative;
  overflow: hidden;
}

.hero-card::before {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at top right,
    rgba(56, 189, 248, 0.12),
    transparent 60%
  );
  opacity: 1;
  pointer-events: none;
}

.hero-avatar /* Real Image Avatar */
.hero-avatar-img {
  width: 90px;
  height: 90px;
  border-radius: 1.1rem;
  object-fit: cover;
  border: 2px solid rgba(99, 102, 241, 0.6);
  box-shadow: 0 18px 40px rgba(56, 189, 248, 0.5);
  background: #0f172a;
}


.hero-small-text {
  font-size: 0.8rem;
  color: #9ca3af;
}

/* =========
   Skills (centered wide grid)
   ========= */

.skills {
  text-align: center;
}

.skills-grid {
  display: grid;
  gap: 1.5rem;
  margin-top: 2.5rem;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.skill-card {
  border-radius: 1.1rem;
  border: 1px solid rgba(55, 65, 81, 0.9);
  background: linear-gradient(145deg, #020617, #020617, #020617);
  padding: 1.2rem 1.3rem;
  box-shadow: 0 14px 40px rgba(0, 0, 0, 0.75);
}

.skill-title {
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 0.4rem;
}

.skill-list {
  list-style: none;
  margin-top: 0.3rem;
}

.skill-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
  color: #9ca3af;
  margin-bottom: 0.25rem;
}

.skill-dot {
  width: 6px;
  height: 6px;
  border-radius: 999px;
  background: #6366f1;
}

/* =========
   Education
   ========= */

.education {
  text-align: center;
}

.timeline {
  margin: 2.5rem auto 0;
  border-left: 1px solid rgba(55, 65, 81, 0.9);
  padding-left: 1.8rem;
  position: relative;
  max-width: 700px;
  text-align: left; /* keep entries readable */
}

.timeline-item {
  position: relative;
  margin-bottom: 2rem;
}

.timeline-dot {
  position: absolute;
  left: -2.4rem;
  top: 0.2rem;
  width: 12px;
  height: 12px;
  border-radius: 999px;
  background: linear-gradient(135deg, #6366f1, #22c55e);
  box-shadow: 0 0 12px rgba(56, 189, 248, 0.8);
}

.timeline-content {
  padding-bottom: 0.4rem;
}

.timeline-title {
  font-size: 1rem;
  font-weight: 600;
}

.timeline-institution {
  font-size: 0.9rem;
  color: #9ca3af;
  margin-top: 0.15rem;
}

.timeline-period {
  font-size: 0.8rem;
  color: #6b7280;
  margin-top: 0.1rem;
}

.timeline-details {
  font-size: 0.9rem;
  color: #9ca3af;
  margin-top: 0.4rem;
}

/* =========
   Projects (centered wide grid)
   ========= */

.projects {
  text-align: center;
}

.projects-grid {
  display: grid;
  gap: 1.5rem;
  margin-top: 2.5rem;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
}

.project-card {
  border-radius: 1.1rem;
  border: 1px solid rgba(55, 65, 81, 0.9);
  background: radial-gradient(circle at top, #0b1120, #020617);
  padding: 1.2rem 1.3rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.85);
  transition: transform 0.15s ease, box-shadow 0.15s ease,
    border-color 0.15s ease;
}

.project-card:hover {
  transform: translateY(-4px);
  border-color: #6366f1;
  box-shadow: 0 24px 60px rgba(79, 70, 229, 0.5);
}

.project-title {
  font-size: 1rem;
  font-weight: 600;
}

.project-description {
  font-size: 0.9rem;
  color: #9ca3af;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.35rem;
  margin-top: 0.3rem;
}

.project-tag {
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  background: rgba(17, 24, 39, 0.9);
  padding: 0.25rem 0.55rem;
  border-radius: 999px;
  color: #d1d5db;
}

.project-link {
  font-size: 0.85rem;
  margin-top: 0.4rem;
  color: #a5b4fc;
}

/* =========
   Services (centered wide grid)
   ========= */

.services {
  text-align: center;
}

.services-grid {
  display: grid;
  gap: 1.5rem;
  margin-top: 2.5rem;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.service-card {
  border-radius: 1.1rem;
  border: 1px solid rgba(55, 65, 81, 0.9);
  background: linear-gradient(145deg, #020617, #0b1120);
  padding: 1.15rem 1.25rem;
  box-shadow: 0 18px 48px rgba(0, 0, 0, 0.85);
}

.service-title {
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.service-description {
  font-size: 0.9rem;
  color: #9ca3af;
}

/* =========
   Contact
   ========= */

.contact {
  text-align: center;
}

.contact-form {
  margin-top: 2.3rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  text-align: left; /* keep form labels aligned */
}

.form-row {
  display: flex;
  gap: 1rem;
}

@media (max-width: 640px) {
  .form-row {
    flex-direction: column;
  }
}

.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.form-group label {
  font-size: 0.85rem;
  color: #d1d5db;
}

.form-group input,
.form-group textarea {
  background: rgba(15, 23, 42, 0.95);
  border: 1px solid rgba(55, 65, 81, 0.9);
  border-radius: 0.9rem;
  padding: 0.65rem 0.85rem;
  font-size: 0.9rem;
  color: #e5e7eb;
  outline: none;
  transition: border-color 0.15s ease, box-shadow 0.15s ease,
    background 0.15s ease;
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: #6b7280;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #6366f1;
  box-shadow: 0 0 0 1px rgba(99, 102, 241, 0.5);
  background: #020617;
}

.contact-extra {
  margin-top: 1.6rem;
  font-size: 0.9rem;
  color: #9ca3af;
}

.contact-extra a {
  color: #a5b4fc;
}

/* =========
   Footer
   ========= */

.footer {
  border-top: 1px solid rgba(31, 41, 55, 0.9);
  background: #020617;
  padding: 1.2rem 0 1.6rem;
}

.footer-inner {
  text-align: center;
  font-size: 0.8rem;
  color: #6b7280;
}

/* ====================================
   Utilities & About Section Styling
   ==================================== */

/* Text alignment */
.text-center {
  text-align: center;
}

/* Margin utilities */
.mb-2 {
  margin-bottom: 0.5rem;
}
.mb-3 {
  margin-bottom: 0.75rem;
}
.mb-4 {
  margin-bottom: 1rem;
}
.mb-6 {
  margin-bottom: 1.5rem;
}
.mb-8 {
  margin-bottom: 2rem;
}

/* Padding utilities */
.p-6 {
  padding: 1.5rem;
}
.p-8 {
  padding: 2rem;
}
.p-10 {
  padding: 2.5rem;
}

/* Flex utilities */
.flex {
  display: flex;
}
.flex-wrap {
  flex-wrap: wrap;
}
.justify-center {
  justify-content: center;
}

/* Gap utilities */
.gap-2 {
  gap: 0.5rem;
}
.gap-3 {
  gap: 0.75rem;
}

/* Rounded */
.rounded {
  border-radius: 0.75rem;
}

/* Width helpers */
.max-w-3xl {
  max-width: 768px;
}
.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

/* Typography utilities */
.text-xs {
  font-size: 0.75rem;
}
.text-sm {
  font-size: 0.875rem;
}
.text-base {
  font-size: 1rem;
}
.text-lg {
  font-size: 1.125rem;
}
.text-3xl {
  font-size: 1.875rem;
}
.font-semibold {
  font-weight: 600;
}

/* Color utilities */
.text-gray-200 {
  color: #e5e7eb;
}
.text-gray-300 {
  color: #d1d5db;
}
.text-gray-400 {
  color: #9ca3af;
}

/* Text transform */
.uppercase {
  text-transform: uppercase;
}

/* Tracking */
.tracking-\[0\.25em\] {
  letter-spacing: 0.25em;
}

/* Line height */
.leading-relaxed {
  line-height: 1.6;
}

/* Gradient text (name, headings) */
.text-gradient {
  background: linear-gradient(90deg, #6366f1, #22c55e, #06b6d4);
  -webkit-background-clip: text;
  color: transparent;
}

/* Generic card (About, skill pills, etc.) */
.card {
  background: rgba(15, 23, 42, 0.95);
  border-radius: 1.5rem;
  border: 1px solid rgba(55, 65, 81, 0.9);
  box-shadow: 0 28px 80px rgba(0, 0, 0, 0.9);
}

/* Skill / tag pills inside About card */
.card span {
  background: rgba(15, 23, 42, 0.95);
  border: 1px solid rgba(55, 65, 81, 0.9);
  font-size: 0.85rem;
  color: #e5e7eb;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.7);
}

/* Blinking cursor for typing animation */
.blinking-cursor {
  display: inline-block;
  width: 1px;
  height: 1.1em;
  background: #e5e7eb;
  margin-left: 2px;
  animation: blink 1s step-start infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

/* Responsive utilities for md: classes used in About */

@media (min-width: 768px) {
  .md\:p-10 {
    padding: 2.5rem;
  }
  .md\:text-4xl {
    font-size: 2.25rem;
  }
  .md\:text-lg {
    font-size: 1.125rem;
  }
  .md\:text-base {
    font-size: 1rem;
  }
  .md\:text-sm {
    font-size: 0.875rem;
  }
  .md\:gap-3 {
    gap: 0.75rem;
  }
}
